.banner {
  margin-top: 50px;
  .block {
    background-color: rgba(202, 123, 128, 0.4);
  }
  .banner__content {
    text-align: center;

    p {
      margin: 0 auto 20px;
      max-width: 600px;
      color: #000;
    }

    h2 {
      color: #000;
      font-weight: 700;
      font-size: 32px;
      max-width: 550px;
      margin: 0 auto;

      &:not(:first-child) {
        margin: 50px auto;
      }
      span {
        color: #ff0000;
      }
    }

    span {
      color: #000;
      margin-top: 10px;
      font-family: Arial, Helvetica, sans-serif;
      display: block;
    }
    a {
      font-weight: 700;
      color: darkred;
      border-bottom: 1px solid darkred;
    }
  }
}
