// Base styles

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
  color: #60646d;
  background-color: #fafafa;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

figure,
p {
  margin: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.block {
  padding: 30px;
  background-color: #fff;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
  margin: 15px auto !important;
}

.container {
  max-width: 1155px;
  margin: 0 auto;
}

.pre-title {
  margin-bottom: 20px;
  font-weight: 500;
}

.title {
  font-size: 44px;
  font-weight: 700;
}

.block-title {
  font-size: 30px;
}

.block-text {
  text-indent: 20px;
  max-width: 70%;
  margin-top: 20px;
  font-size: 17px;
  line-height: 1.5rem;
}
